

import React, {useState} from "react";

import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { SvgIcon_Cmpt } from "../../../components";
import { getStore, updStore, gen16CodeId } from "../../../stores/Utils";

const Svg = SvgIcon_Cmpt();



const SettingModal  = ({ data, confirm, handleClick, close }) => {
	const [obs, set_obs] = useState(0);

	const { modalID } = data;

	const handleClose =  (v) => {
		CloseModal({id:modalID})
    }
    
    const handleConfirm =  () => {
		
        confirm();
		handleClose();
	}

	const handlePrintStyle =  (v) => {

		if(v && !getStore("modernFontReport")){
			updStore("modernFontReport",v);
			set_obs(gen16CodeId())
		}
		else if(!v && getStore("modernFontReport")){
			updStore("modernFontReport",v);
			set_obs(gen16CodeId())
		}
	}
	
	const handleTheme =  () => {
		let darkTheme = getStore("darkTheme")?true:false;
		let nTheme = !darkTheme;

		const el = document.querySelector('[theme*="92357239"]');
		if(el){
			el.className = nTheme? "light_theme palette_body App ":"dark_theme  palette_body App ";
		};
		let html = document.children[0];
		html.style.backgroundColor =  nTheme? "#f1f1f1": "#212121";
		updStore("darkTheme",nTheme);
		set_obs(gen16CodeId())

		
		
    }
	
    return (
		<div style={{
			maxHeight: "80vh",
			minHeight:  "40vh",
			width:"560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px",
			
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Settings
				</p>
				<div className="flexSpace"/>
				
			</div>
			<div className="_dsplFlx ">
				<span>
					
					
				</span>
			</div>
			<div className="theme_setting_box ">

				<div className="_dsplFlx spaceAround ">
					
					<div className={`theme_style`} onClick={handleTheme}>
					
					<Svg name={"theme"}	color={"var(--hrm_palette-general-background-b)"}/>
						<div className="title gSansFont">
							{getStore("darkTheme")?"Dark":"Light"} 
						</div>
					</div>
				</div>
			</div>

			<div className="print_setting_box ">

							
				<div className="header ">
					<span>
						Estilo de Impresion
					</span>
				</div>
				<div className="_dsplFlx spaceAround ">

					<div className={`print_style modern ${getStore("modernFontReport")?"active":""}`}  onClick={()=>handlePrintStyle(true)}>
						<Svg 
							name={"check_circle"}	/>
						<div className="title">
							Modern
						</div>
						<div className="body">
							<h4>
								Fuente Normal
							</h4>
							<h5>
								Fuente Negrita
							</h5>
						</div>
					</div>
					<div className={`print_style classic ${getStore("modernFontReport")?"":"active"}`} onClick={()=>handlePrintStyle(false)}>
					
							<Svg 
							name={"check_circle"}	/>
						
						<div className="title">
							Classic
						</div>
						<div className="body">
							<h4>
								Fuente Normal
							</h4>
							<h5>
								Fuente Negrita
							</h5>
						</div>
					</div>
				</div>
			</div>


			<div className="_dsplFlx " style={{padding:"15px 10px"}}>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={handleClose}>
					Cerrar
				</p>
				<p className="group_title addBtn purple" onClick={handleConfirm}>
					Guardar
				</p>
			</div>

		</div>
    )   
}

export default SettingModal;




