import React, { useEffect, useState } from "react";
import "./styles.css"
import { SvgIcon_Cmpt } from "../../../components";
import { getSvgSymbols, ObjectKeys, getStore, updStore } from "../../../stores/Utils";
import { NavLink } from "react-router-dom";
import { OpenModal } from "../../../components/DialogHRM";
import SettingModal from "./SettingModal";

const Svg = SvgIcon_Cmpt();



const FloatingSideMenu  = ({ data, active, updTab }) => {
	
	const confirm =  () => {

	}
	const OpenSetting =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <SettingModal confirm={confirm} item={data} />
			OpenModal(modaldata);
		}
	}
	

	return (
			<div className="dashboard_menu">
				<div className="dashboard_fixed" style={{margin: 5, borderRight: "1px solid var(--ytcp-text-secondary);)"}}>
					
					<div className={"option_list"}>
					{data?.map(tb=>{
						return (
							<NavLink
							key={tb.index}
								to={{ pathname: tb.path}}
							>
							<div 
							
								onClick={()=>updTab?.(tb.index)}
								className="menu_item _dsplFlx" 
								style={
									tb.index===active?{
										"--ytcp-themed-red": "#b00000",
										"--ytcp-text-secondary2": "#b00000",
										"--ytcp-text-secondary": "var(--hrm_palette-blue-purple)",
										"--ytcp-opacity_line": 1,
										"--ytcp-general-background-z": "var(--hrm_palette-blue-purple_100)"
									}:{
										"--ytcp-text-secondary": "var(--hrm_palette-float-gray_5A)"
									}
								}
							>
								<div className="side_mark">

								</div>
								<div className="icon">
									<Svg name={tb.icon} color={tb.index===active?"var(--hrm_palette-blue-purple)":"var(--hrm_palette-float-gray_5A)"}/>
								</div>
								<div className="lbl">{tb.label}</div>
							</div>
							</NavLink>
						)
					})}
					</div>
					<div  className="version_box_fixed">
						<div className="_dsplFlx">
							<div className="icon" style={{marginTop:7}} onClick={OpenSetting}>
									<Svg name={"setting"} size={20}/>
							</div>

							<p className={"email_prf"}>
								{ getStore("profileUser").email}
							</p>
						</div>
						<p className={"cpr_vrs"}>
							@HrmFinance V23.1
						</p>
					</div>
			</div>
				
			</div>
	);
};

export default FloatingSideMenu;

